* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  &::before,
  &::after {
    box-sizing: border-box;
  }
  &:focus {
    outline: 0;
    box-shadow: none;
    border: none;
  }
}
html, body {
  height: 100%;
  background-color: beige;
  font: 14px/18px Arial, sans-serif;
}
@media (any-hover: hover) {
  //scrollbar
  ::-webkit-scrollbar {
    width: 8px;
    height: 8px;

    &-track {
      background: #fff;
    }
    &-thumb {
      background: #ccc;
      border-radius: 4px;

      &:hover {
        background: #999;
      }
    }
  }
  @-moz-document url-prefix() {
    body {
      scrollbar-color: #E3D3BF transparent;
      scrollbar-width: thin;
    }
  }
}

#root {
  height: 100%;
}

a {
  text-decoration: none;
  color: #123a3b;
  font-weight: normal;
  transition: .15s;

  &:hover {
    color: #448688;
  }
  &:active {
    color: #000;
  }
}
button {
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.nav ul {
  list-style: none;
  padding: 0;
}
.input {
  border: 1px solid #123a3b;
  background-color: transparent;
  min-height: 32px;
  padding: 0 8px;
  font-size: 14px;
  line-height: 100%;
  border-radius: 16px;

  &:focus {
    outline: 0;

    &::placeholder {
      opacity: 0;
    }
  }
}
.offset-h-sm {
  padding: 0 16px;
}
.icon-info {
  font-family: "Segoe UI Variable", serif;
  font-weight: 600;
  font-size: 18px;
  background-color: dodgerblue;
  color: yellow;
  width: 20px;
  height: 20px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}
[data-hint] {
  cursor: pointer;
}
[data-hint]::before {
  opacity: 0;
  visibility: hidden;
}
[data-hint]:hover::before {
  content: attr(data-hint);
  color: #fff;
  background-color: #010118;
  position: absolute;
  padding: 4px 8px;
  font-size: 12px;
  font-weight: 400;
  font-family: Arial, sans-serif;
  margin-top: 56px;
  opacity: 1;
  visibility: visible;
  z-index: 1;
}
//typographic
h1, h2, h3, h4, h5, h6 {
  padding: 16px 0;
}